.header {
    position: absolute;
    width: 100%;
    height: 50px;
    background: #FFFFFF;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);
}

.btn-init {
    background: #0057FF;
    text-align: center;
    color: #FFFFFF;
    font-family: 'Inter';
    font-style: normal;
}
.btn-init:hover {
    cursor: pointer;
}

.btn-connectWallet{
    position: absolute;
    width: 147px;
    height: 26px;
    right: 15vw;
    top: 12px;
    padding-top: 3px;
    font-size: 12px;
    border-radius: 25px;
}

.btn-connectWallet:hover .disconnect-board {
    display: block;
}

.disconnect-board {
    display: none;
    background-color: #f1f1f1;
    margin-top: 5px;
    margin-left: 13px;
    padding: 5px;
    width: 120px;
    height: 40px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    position: absolute;
    color: black;
    z-index: 1;
    font-size: 16px;
}

.btn-submit {
    width: 34%;
    height: 34px;
    margin-left: 32%;
    padding-top: 4px;
    font-size: 16px;
    border-radius: 10px;
}



.header-text {
    position: absolute;
    width: 92px;
    height: 19px;
    left: 20vw;
    top: 18px;

    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;

    color: #000000;
}


.middle {
    position: absolute;
    width: 100%;
    height: 475px;
    left: 0px;
    top: 50px;

    background: #0057FF;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.25);
}

.middle-text-1 {
    position: absolute;
    width: 417px;
    height: 91px;
    left: 20vw;
    top: 106px;
    
    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 64px;
    line-height: 77px;
    
    color: #FFFFFF;
}
.middle-text-2 {
    position: absolute;
    width: 337px;
    height: 122px;
    left: 20vw;
    top: 197px;

    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 39px;

    color: #FFFFFF;
}
.middle-text-3 {
    position: absolute;
    width: 336px;
    height: 34px;
    left: 20vw;
    top: 361px;

    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    display: flex;
    align-items: center;

    color: #FFFFFF;
}
.middle-text-4 {
    position: absolute;
    width: 377px;
    height: 34px;
    right: 19.5vw;
    top: 70px;

    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 29px;
    display: flex;
    align-items: center;
    text-align: center;

    color: #FFFFFF;
}
.input-board {
    position: absolute;
    width: 419px;
    height: 289px;
    right: 20vw;
    top: 129px;

    background: #0085FF;
    border-radius: 20px;
    padding-top: 31px;
}

.input-init {
    width: 78%;
    height: 37px;

    outline: 0; 
    border: 0px;
    background: #FFFFFF;
    border-radius: 10px;

    padding-left: 7%;
    padding-right: 7%;

    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;

    margin-bottom: 13px !important;
    margin-left: 11% !important;
}

.select {
    width: 78% !important;
    height: 37px;

    background: #FFFFFF;
    border-radius: 10px !important;
    
    padding-left: 7% !important;

    font-family: 'Inter';
    font-size: 14px !important;

    margin-bottom: 13px;
    margin-left: 11%;

    background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3E%3Cpath fill='none' stroke='%23343a40' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='M2 5l6 6 6-6'/%3E%3C/svg%3E") !important;
    background-repeat: no-repeat !important;
    background-position: right 0.75rem center !important;
    background-size: 16px 12px !important;
}


.footer {
    display: flex;
    align-items: center;
    justify-content: center;
}
.footer-text {
    position: absolute;
    width: 668px;
    height: 58px;
    top: 569px;
    
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 39px;
    text-align: center;
    
    color: #2E2E2E;
}

.display-table {
    margin-top: 612px;
    height: 330px;
    width: 68vw;
    padding-top: 20px;
    padding-bottom: 15px;
    padding-right: 4vw;
    padding-left: 4vw;
    overflow-x: auto;
}

.table-board {
    padding: 22px;
    height: 269px;

    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.25);
    border-radius: 15px;
}

table{
    border-collapse: collapse;
}
tr {
    width: 100%;
    height: 35px;
    padding-left: 22px;
    border-bottom: 1px solid #DDDDDD;

    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    align-items: flex-end;
}

th {
    padding-left: 22px;
    color: #959595;
    text-align: left !important;
}

td {
    padding-left: 22px;
    color: #000000;
}

.connectWalletModal {
    max-width: 20vw;
    min-width: 300px;
    margin-left: 40vw;
}

.walletButton {
    width: 100%;
    height: 55px;
    margin-bottom: 10px;
    margin-top: 10px;
    color: black !important;
    background-color: transparent !important;
    border: 1px solid black !important;
    border-radius: 120px !important;
}

@media screen and (max-width: 970px) {
    .table-board {
        width: 1000px;
    }
}

@media screen and (max-width: 1320px) {
    .middle {
        height: 789px;
    }
    .middle-text-1 {
        top: 61px;
    }
    .middle-text-2 {
        top: 169px;
    }
    .middle-text-3 {
        top: 297px;
    }
    .footer-text {
        top: 877px;
    }
    .middle-text-4 {
        top: 400px;
        left: 38vw;
    }
    .input-board {
        left: 20vw;
        right: 32px;
        top: 455px;
        width: 60vw;
    }
    .input-init {
        margin-left: 5% !important;
        padding-left: 3%;
        padding-right: 3%;
        width: 90%;
    }
    .select {
        margin-left: 5%;
        padding-left: 3% !important;
        padding-right: 3%;
        width: 90% !important;
    }
    .footer-text {
        top: 877px;
    }
    .display-table {
        margin-top: 926px;
    }
    .connectWalletModal {
        margin-left: 30vw;
    }
}

@media screen and (max-width: 768px) {
    .middle-text-4 {
        left: 30vw;
    }
    .footer-text {
        font-size: 20px;
    }
}

@media screen and (max-width: 630px) {
    .middle-text-4 {
        left: 25vw;
    }
    .connectWalletModal {
        margin-left: 20vw;
    }
}

@media screen and (max-width: 564px) {
    .middle-text-4 {
        left: 23vw;
    }
}

@media screen and (max-width: 480px) {
    .header-text {
        left: 32px;
    }
    .btn-connectWallet{
        right: 31px;
    }

    .middle-text-1 {
        top: 61px;
        left: 32px;
    }
    .middle-text-2 {
        top: 169px;
        left: 32px;
    }
    .middle-text-3 {
        top: 297px;
        left: 32px;
    }
    .middle-text-4 {
        left: 58px;
    }
    .input-board {
        left: 32px;
        width: 80vw;
    }
    .input-init {
        margin-left: 11% !important;
        padding-left: 7%;
        padding-right: 7%;
        width: 78%;
    }
    .select {
        margin-left: 11%;
        padding-left: 7% !important;
        padding-right: 7%;
        width: 78% !important;
    }
    /* .display-table {
        left: 0;
        padding-left: 32px;
    } */
    .connectWalletModal {
        margin-left: 20vw;
    }
}





